@import 'utils/reset';
@import 'utils/common';
@import '_header';
@import '_footer';
@import '_main-slider';

@import '~/node_modules/swiper/css/swiper.min.css';

html {
  line-height: $mainLineHeight;
  font-size: $mainFontSize;
  color: $mainFontColor;
  height: 100%;
  @media screen and (max-width: $desktopWidth) {
    font-size: 16px;
  }
  @media screen and (max-width: $tableWidth) {
    font-size: 15px;
  }
  @media screen and (max-width: $phoneWidth) {
    font-size: 14px;
  }
}

body {
  margin: 0;
  padding: 0;
  font-family: $mainFont;
  font-size: $mainFontSize;
  font-weight: $mainFontWeight;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fff;
  color: $mainFontColor;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

a {
  text-decoration: none;
  color: inherit;
  text-decoration-skip-ink: none;
}
button {
  cursor: pointer;
}
ul,
li {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

section,
.section {
  position: relative;
  padding: 40px 0;
  @media screen and (max-width: $desktopWidth) {
    padding: 36px 0;
  }
  @media screen and (max-width: $desktopWidth) {
    padding: 28px 0;
  }
  @media screen and (max-width: $desktopWidth) {
    padding: 20px 0;
  }
}

.wrapper {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  min-height: 100vh;
}

.content-wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.container {
  width: 100%;
  flex: 0 0 auto;
  position: relative;
  margin: 0 auto;
  max-width: $desktopWidth;
  @media screen and (max-width: $smDesktopWidth) {
    max-width: tableWidth;
  }
  @media screen and (max-width: $tableWidth) {
    max-width: phoneWidth;
  }
  @media screen and (max-width: $phoneWidth) {
    max-width: smPhoneWidth;
  }
}

.page {
  &__header {
  }
  &__footer {
  }
}
.main-content-wrapper {
  display: flex;
}
/*------------------------------------*\
    #CATALOG CATEGORIES BLOCK START
\*------------------------------------*/

.catalog-categories {
  display: flex;
  flex-wrap: wrap;
  margin: 0 -35px -65px;

  &__item {
  }
}
.item-catalog-category {
  flex: 0 1 calc(100% / 3 - 70px);
  margin: 0 35px 70px;
  display: flex;
  align-items: center;
  background-color: #f3f3f3;
  padding: 45px 200px 45px 35px;
  position: relative;
  height: 217px;
  transition: $transition;

  &:hover,
  &:focus,
  &:active {
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.25);
  }

  &__count {
    font-weight: 500;
    color: #666666;
  }

  &__title {
    font-weight: 600;
    font-size: 32px;
    line-height: 29px;
  }

  &__more {
    color: #c02d33;
    font-weight: 500;
    text-decoration: underline;
    margin-top: 45px;

    &:hover,
    &:focus,
    &:active {
      text-decoration: none;
    }
  }
  &__image {
    position: absolute;
    top: 0;
    right: 0;
    width: auto;
    display: block;
    max-height: 100%;
  }
}

/*------------------------------------*\
    #CATALOG CATEGORIES BLOCK END
\*------------------------------------*/

/*------------------------------------*\
    #ADVANTAGES BLOCK START
\*------------------------------------*/

.advantages-block {
  background-color: #00672f;
  color: #fff;

  &__container {
    display: flex;
    padding: 45px 0;
    justify-content: space-between;
  }

  &__item {
    display: flex;
    align-items: center;
    padding-right: 50px;
  }
}

.item-advantage {
  &__icon {
    margin-right: 30px;
  }

  &__content {
  }

  &__title {
    font-weight: 600;
    font-size: 22px;
    line-height: 1.125;
  }

  &__text {
    font-size: 14px;
    color: #efefef;
    margin-top: 3px;
    line-height: 1.214;
  }
}

/*------------------------------------*\
    #ADVANTAGES BLOCK END
\*------------------------------------*/

/*------------------------------------*\
    #PROMO BLOCKS START
\*------------------------------------*/

.promo-blocks {
  display: flex;
  margin: 0 -35px;
  flex-wrap: wrap;

  &__item {
    box-shadow: 25px 25px 0 0 #e1e1e1;
    margin: 0 35px;
    flex: 0 0 calc(100% / 3 - 70px);
  }
}
.item-promo {
  background-repeat: no-repeat;
  background-size: cover;
  min-height: 470px;
  padding: 350px 35px 30px;

  &--delivery {
    background-image: url(../img/bg-promo-1.jpg);
  }

  &--sizes {
    background-image: url(../img/bg-promo-2.jpg);
  }

  &--night {
    background-image: url(../img/bg-promo-3.jpg);
  }

  &__header {
  }

  &__title {
    font-weight: 600;
    font-size: 32px;
    color: #ffffff;
    line-height: 1.083;
  }

  &__text {
    font-weight: 500;
    font-size: 16px;
    color: #eeeeee;
    margin-top: 4px;
  }

  &__more {
    font-weight: 500;
    color: #c02d33;
    transition: $transition;
    padding: 5px 15px;
    background-color: #fff;
    margin-top: 20px;
    display: inline-block;

    &:hover,
    &:focus,
    &:active {
      color: #fff;
      padding: 5px 15px;
      background-color: $primaryColor;
    }
  }
}

/*------------------------------------*\
    #PROMO BLOCKS END
\*------------------------------------*/

/*------------------------------------*\
    #ABOUT BLOCK START
\*------------------------------------*/

.about-block {
  background-repeat: no-repeat;
  background-image: url(../img/bg-about.jpg);
  background-position: right center;
  padding: 60px 0;

  &__title {
    font-weight: 600;
    font-size: 32px;
    line-height: 39px;
    text-transform: uppercase;
    color: #0d0d0d;
    max-width: 820px;
  }

  &__text {
    padding: 30px 30px 30px 0;
    max-width: 1060px;
    background-color: #fff;
  }
}

/*------------------------------------*\
    #ABOUT BLOCK END
\*------------------------------------*/

/*------------------------------------*\
    #SIDEBAR START
\*------------------------------------*/

.sidebar {
  border-right: 1px solid #d1d1d1;
  box-shadow: 1px 0px 4px rgba(0, 0, 0, 0.1);
  background-color: #fff;
  max-width: 340px;
  width: 100%;

  &__block {
    margin-bottom: 15px;
  }
}
.sidebar-block {
  &__title {
    font-weight: 600;
    color: #0d0d0d;
    padding: 16px 15px 17px 40px;
  }
}

/*------------------------------------*\
    #SIDEBAR END
\*------------------------------------*/

/*------------------------------------*\
    #CATALOG SIDEBAR START
\*------------------------------------*/
.sidebar {
  &__menu {
  }
}
.menu-catalog-sidebar {
  &__item {
    position: relative;
    border-top: 1px solid #d1d1d1;
    &:last-child {
      border-bottom: 1px solid #d1d1d1;
    }
  }

  &__link {
    display: flex;
    align-items: center;
    padding: 10px 60px 10px 40px;
  }
  &__icon {
    width: 38px;
    max-height: 38px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    margin-right: 20px;

    .icon {
      width: 38px;
      max-height: 38px;
    }
  }
  &__text {
  }
  .menu-catalog-sidebar__btn {
    width: 10px;
    height: 10px;
    border: 2px solid #939393;
    transform: rotate(45deg);
    border-left: 0;
    border-bottom: 0;
    position: absolute;
    right: 15px;
    top: 50%;
    margin-top: -5px;
  }
}

/*------------------------------------*\
    #CATALOG SIDEBAR END
\*------------------------------------*/

/*------------------------------------*\
    #DELIVERY BLOCK START
\*------------------------------------*/
.sidebar__delivery.delivery-block {
  padding-right: 15px;
  font-size: 14px;
}
/*------------------------------------*\
    #DELIVERY BLOCK END
\*------------------------------------*/
